*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.gallery, .header {
  width: 100%;
  padding: 20px 0;
}

.gallery__container, .header__container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

.card__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.card {
  text-decoration: none;
}

html, body {
  scroll-behavior: smooth;
}

body {
  background-color: #e6e6e6;
  font-family: Roboto, sans-serif;
}

.main {
  padding-top: 80px;
}

.header {
  z-index: 10;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background-color: rgba(255, 255, 255, .627);
  border-bottom: 1px solid rgba(170, 170, 170, .5);
  position: fixed;
  top: 0;
  left: 0;
  box-shadow: 0 1px 3px rgba(238, 238, 238, .204), 0 1px 1px rgba(170, 170, 170, .204), 0 2px 1px rgba(238, 238, 238, .204);
}

.header__container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.search-form__input {
  min-width: 240px;
  max-width: 400px;
  height: 40px;
  background-color: rgba(255, 255, 255, .753);
  border: 1px solid #a1a1a1;
  border-right-width: 0;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  outline: rgba(0, 0, 0, 0);
  flex-grow: 1;
  padding: 5px 10px 5px 20px;
  transition: border-color .2s ease-in-out, box-shadow .2s ease-in-out;
}

.search-form__input:hover, .search-form__input:focus {
  border-color: #414141;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.search-form__submit {
  min-width: 40px;
  height: 40px;
  color: #818181;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .753);
  border: 1px solid #a1a1a1;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  outline: rgba(0, 0, 0, 0);
  align-items: center;
  transition: border-color .2s ease-in-out, color .2s ease-in-out, box-shadow .2s ease-in-out;
  display: flex;
}

.search-form__submit:hover, .search-form__submit:focus {
  color: #414141;
  border-color: #414141;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.search-form__icon {
  fill: currentColor;
}

.loader {
  width: 2.5em;
  height: 2.5em;
  text-indent: -9999em;
  color: #212121;
  border-radius: 50%;
  font-size: 5px;
  animation: loading 1.8s ease-in-out -.16s infinite both;
  position: absolute;
  bottom: -3px;
  left: 50%;
  transform: translateZ(0);
}

.loader.is-hidden {
  display: none;
}

.loader:before {
  content: "";
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  animation: loading 1.8s ease-in-out -.32s infinite both;
  position: absolute;
  top: 0;
  left: -3.5em;
}

.loader:after {
  content: "";
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  animation: loading 1.8s ease-in-out infinite both;
  position: absolute;
  top: 0;
  left: 3.5em;
}

@keyframes loading {
  0%, 80%, 100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }

  40% {
    box-shadow: 0 2.5em;
  }
}

.gallery__content {
  width: 100%;
  grid-template-columns: 1fr;
  gap: 20px;
  display: grid;
}

@media screen and (min-width: 640px) {
  .gallery__content {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (min-width: 960px) {
  .gallery__content {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (min-width: 1200px) {
  .gallery__content {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}

@media screen and (min-width: 1600px) {
  .gallery__content {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  }
}

@media screen and (min-width: 2100px) {
  .gallery__content {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
  }
}

.card {
  color: inherit;
  cursor: zoom-in;
  outline: rgba(0, 0, 0, 0);
  font-size: 12px;
}

.card__container {
  width: 100%;
  height: 100%;
  background-color: #f6f6f6;
  border: 1px solid #eee;
  border-radius: 10px;
  flex-direction: column;
  transition: box-shadow .2s ease-in-out, transform .2s ease-in-out;
  display: flex;
  overflow: hidden;
  box-shadow: 0 1px 3px rgba(238, 238, 238, .204), 0 1px 1px rgba(170, 170, 170, .204), 0 2px 1px rgba(238, 238, 238, .204);
}

.card:hover .card__container, .card:focus .card__container {
  transform: scale(1.05);
  box-shadow: 0 1px 1px rgba(0, 0, 0, .12), 0 4px 4px rgba(0, 0, 0, .06), 1px 4px 6px rgba(0, 0, 0, .16);
}

.card__image-container {
  flex-grow: 1;
  overflow: hidden;
}

.card__image {
  transition: transform .2s ease-in-out;
}

.card:hover .card__image, .card:focus .card__image {
  transform: scale(1.1);
}

.card__info {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.card__info-item {
  width: 100%;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding-top: 8px;
  padding-bottom: 5px;
  display: flex;
  position: relative;
}

.card__info-item:not(:last-child):after {
  content: "";
  width: 100%;
  height: 100%;
  border-right: 1px solid #eee;
  display: block;
  position: absolute;
  top: 0;
  right: 0;
}

.card__item-count {
  margin-top: 3px;
  font-size: 10px;
}

body.hidden-scroll {
  overflow: hidden;
}

